import axios from "axios";
import { useState } from "react";
import { useEffect } from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { addItemToCart } from "../features/counterSlice";
import "../styles/routes/Restaraunt.scss";

export default function Restaraunt() {
  const dispatch = useDispatch();
  const { id } = useSelector((state) => state.counter);
  const [data, setData] = useState();

  const getData = async () => {
    // const restaraunt = await axios.get(
    //   `https://localhost:7262/Restaraunt/${id}`
    // );
    if (window.location.href.indexOf("2") > -1) {
      setData({
        id: 2,
        name: "SushiYa",
        address: "Vilyamsa St. 85",
        rating: 3,
        logo: "https://cdn1.pokupon.ua/uploaded/merchant_pages/2069/logos/original/su-logo.png",
        menu: [
          {
            id: 1,
            name: "Filadelphia",
            price: 80,
            weight: 120,
            logo: "http://edimvkusno.org/images/materialsimages/filadelfia.jpg",
            rating: 5,
          },
          {
            id: 2,
            name: "Maki",
            price: 40,
            weight: 50,
            logo: "https://img3.zakaz.ua/upload7db9ac5476c451b306b945ea161bc99a.jpg.350x.jpg",
            rating: 5,
          },
        ],
      });
    } else {
      setData({
        id: 1,
        name: "McDonald's",
        address: "Vilyamsa St. 85",
        rating: 5,
        logo: "https://upload.wikimedia.org/wikipedia/commons/thumb/3/36/McDonald%27s_Golden_Arches.svg/1200px-McDonald%27s_Golden_Arches.svg.png",
        menu: [
          {
            id: 1,
            name: "Cheeseburger",
            price: 8,
            weight: 20,
            logo: "https://s7d1.scene7.com/is/image/mcdonalds/t-mcdonalds-Cheeseburger-1:1-3-product-tile-desktop?wid=829&hei=515&dpr=off",
            rating: 5,
          },
          {
            id: 2,
            name: "Burger",
            price: 8,
            weight: 20,
            logo: "https://s7d1.scene7.com/is/image/mcdonalds/t-mcdonalds-Cheeseburger-1:1-3-product-tile-desktop?wid=829&hei=515&dpr=off",
            rating: 5,
          },
          {
            id: 1,
            name: "Cheeseburger",
            price: 8,
            weight: 20,
            logo: "https://s7d1.scene7.com/is/image/mcdonalds/t-mcdonalds-Cheeseburger-1:1-3-product-tile-desktop?wid=829&hei=515&dpr=off",
            rating: 5,
          },
          {
            id: 2,
            name: "Burger",
            price: 8,
            weight: 20,
            logo: "https://s7d1.scene7.com/is/image/mcdonalds/t-mcdonalds-Cheeseburger-1:1-3-product-tile-desktop?wid=829&hei=515&dpr=off",
            rating: 5,
          },
        ],
      });
    }
  };

  useEffect(() => {
    getData();
  }, []);

  if (data != null) {
    return (
      <div className="wrapper">
        <h1 className="title">{data.name}</h1>
        <div className="logo">
          <img src={data.logo} />
        </div>
        <h3 className="title-menu">Menu</h3>
        <div className="menu-wrapper">
          {data.menu.map((item, index) => (
            <div className="menu-wrapper-element" key={index}>
              <div className="menu-wrapper-element-logo">
                <img src={item.logo} />
              </div>
              <h3 className="menu-wrapper-element-title">{item.name}</h3>
              <div className="menu-wrapper-element-info">
                <p>{item.weight}g</p>
                <p>{item.rating} out of 5 rating</p>
              </div>
              <h3 className="menu-wrapper-element-price">{item.price}$</h3>
              <button
                className="menu-wrapper-element-order"
                onClick={() => {
                  let payload = {
                    id: item.id,
                    logo: item.logo,
                    name: item.name,
                    weight: item.weight,
                    price: item.price,
                  };
                  alert("Item added successfully!");
                  dispatch(addItemToCart(payload));
                }}
              >
                Order
              </button>
            </div>
          ))}
        </div>
      </div>
    );
  }
}
