import { useEffect } from "react";
import "../styles/routes/Menu.scss";
import axios from "axios";
import { useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { setId } from "../features/counterSlice";

export default function Menu() {
  const [restaraunts, setRestaraunts] = useState([]);
  const dispatch = useDispatch();

  useEffect(() => {
    setRestaraunts([]);
    // axios.get("https://localhost:7262/Restaraunt").then((res) => {
    //       });
    setRestaraunts([
      {
        id: 1,
        name: "McDonald's",
        address: "Vilyamsa St. 85",
        rating: 5,
        logo: "https://upload.wikimedia.org/wikipedia/commons/thumb/3/36/McDonald%27s_Golden_Arches.svg/1200px-McDonald%27s_Golden_Arches.svg.png",
        menu: [
          {
            id: 1,
            name: "Cheeseburger",
            price: 8,
            weight: 20,
            logo: "https://s7d1.scene7.com/is/image/mcdonalds/t-mcdonalds-Cheeseburger-1:1-3-product-tile-desktop?wid=829&hei=515&dpr=off",
            rating: 5,
          },
          {
            id: 2,
            name: "Burger",
            price: 8,
            weight: 20,
            logo: "https://s7d1.scene7.com/is/image/mcdonalds/t-mcdonalds-Cheeseburger-1:1-3-product-tile-desktop?wid=829&hei=515&dpr=off",
            rating: 5,
          },
          {
            id: 1,
            name: "Cheeseburger",
            price: 8,
            weight: 20,
            logo: "https://s7d1.scene7.com/is/image/mcdonalds/t-mcdonalds-Cheeseburger-1:1-3-product-tile-desktop?wid=829&hei=515&dpr=off",
            rating: 5,
          },
          {
            id: 2,
            name: "Burger",
            price: 8,
            weight: 20,
            logo: "https://s7d1.scene7.com/is/image/mcdonalds/t-mcdonalds-Cheeseburger-1:1-3-product-tile-desktop?wid=829&hei=515&dpr=off",
            rating: 5,
          },
        ],
      },
      {
        id: 2,
        name: "SushiYa",
        address: "Vilyamsa St. 85",
        rating: 3,
        logo: "https://cdn1.pokupon.ua/uploaded/merchant_pages/2069/logos/original/su-logo.png",
        menu: [
          {
            id: 1,
            name: "Filadelphia",
            price: 80,
            weight: 120,
            logo: "http://edimvkusno.org/images/materialsimages/filadelfia.jpg",
            rating: 5,
          },
          {
            id: 2,
            name: "Maki",
            price: 40,
            weight: 50,
            logo: "https://img3.zakaz.ua/upload7db9ac5476c451b306b945ea161bc99a.jpg.350x.jpg",
            rating: 5,
          },
        ],
      },
    ]);
  }, []);

  function generateLink(itemId) {
    var generatedLink = `/restaraunt/${itemId}`;
    return generatedLink;
  }

  return (
    <main className="wrapper">
      <h2 className="main-title">Restaraunt list</h2>
      <div className="restaraunts-wrapper">
        {restaraunts.map((item, index) => (
          <div key={index} className="restaraunt-position">
            <div className="restaraunt-position-logo">
              <img src={item.logo} alt="restarauntLogo" />
            </div>
            <div className="restaraunt-position-name">
              <h3>{item.name}</h3>
              <p>{item.address}</p>
            </div>
            <div className="restaraunt-position-rating">
              <h3>Rating</h3>
              <p>{item.rating} out of 5</p>
            </div>
            <div className="restaraunt-position-menu">
              <button
                onClick={() => {
                  dispatch(setId(item.id));
                }}
              >
                <Link to={generateLink(item.id)} className="link">
                  Order
                </Link>
              </button>
            </div>
          </div>
        ))}
      </div>
    </main>
  );
}
